import Colors from "../util/Color";
import BinSearchArray from "../util/BinSearch";

export const UserErrorCode = {
    none: 0,
    nan: 1,
    tempChat: 2,
    userNotExisting: 3,
    blocked: 4,
    isSelf: 5
};

export async function getUserNormalChat(uid){
    /*
            does the user exist & does normal chat exist
         */
    if(this.users.getIndex(uid) !== -1){
        /*
            does a normalChat exist at the user
         */
        if(this.users.get(uid).normalChat !== 0) {
            return {
                userExists: true,
                //does the user exist in client
                isUserSaved: true,
                //does a normalChat exist for this user
                chatExists: true,
                //is user self blocked by this user
                isUserBlocked: false,
                //does a temporary chat exist
                tempChat: false
            };
        }else{
            /*
                chat does not exist in server
                it gets created
             */
            const user = this.users.get(uid);
            this.temporaryChat.createNew(uid,user.username);

            return {
                userExists: true,
                //does the user exist in client
                isUserSaved: true,
                //does a normalChat exist for this user
                chatExists: false,
                //is user self blocked by this user
                isUserBlocked: false,
                //does a temporary chat exist
                tempChat: false
            };
        }
        /*
            does there exist a temporary chat with this user
        */
    }else if(this.temporaryChat.doesExist(uid)){
        /*
            user and chat does not exist in server
         */
        return {
            userExists: true,
            //does the user exist in client
            isUserSaved: false,
            //does a normalChat exist for this user
            chatExists: false,
            //is user self blocked by this user
            isUserBlocked: false,
            //does a temporary chat exist
            tempChat: true
        };
    }else{
        /*
            request user from server
         */
        return  await this.temporaryChat.requestUser(uid);
    }
}

export default class User{

    _uid;
    _username;
    _color;
    _online;
    //ids der chats des Users
    _normalChat = 0;
    //ids of the groupchats
    _groupChats = new BinSearchArray();

    constructor(uid,username,online) {
        this.uid = uid;
        this.username = username;
        this.color = Colors.random();
        this.online = online;
    }
    /*
        groupChat is added
     */
    addGroupChat(gcid){
        this.groupChats.add(gcid,gcid);
    }

    removeGroupChat(gcid){
        this.groupChats.remove(gcid);
    }

    get uid() {
        return this._uid;
    }

    set uid(value) {
        this._uid = value;
    }

    get username() {
        return this._username;
    }

    set username(value) {
        this._username = value;
    }

    get color() {
        return this._color;
    }

    set color(value) {
        this._color = value;
    }

    get online() {
        return this._online;
    }

    set online(value) {
        this._online = value;
    }

    get normalChat() {
        return this._normalChat;
    }

    set normalChat(value) {
        this._normalChat = value;
    }

    get groupChats() {
        return this._groupChats;
    }

    set groupChats(value) {
        this._groupChats = value;
    }
}